import { useCallback, useEffect, useMemo, useState } from "react";
import { printer as Printer, types, WebUSB } from "thermal-printer";
import "./App.css";

async function urlToRaw(src) {
  return new Promise((resolve, reject) => {
    const img = new Image();
    img.src = src;
    img.onerror = reject;
    img.onload = () => {
      const canvas = document.createElement("canvas");
      const width = 600 - 24;
      const height = (img.height * width) / img.width;
      canvas.width = width;
      canvas.height = height;
      const context = canvas.getContext("2d");
      context.fillStyle = "#00000060";
      context.fillRect(0, 0, width, height);
      context.drawImage(img, 0, 0, width, height);
      const imageData = context.getImageData(0, 0, width, height);
      resolve(imageData);
      canvas.remove();
    };
  });
}

function useThermalPrinter() {
  const isSupported = useMemo(() => WebUSB.isWebUSBAvailable(), []);
  const [printer, setPrinter] = useState(null);
  const [connected, setConnected] = useState(false);

  const restore = useCallback(async () => {
    if (localStorage.getItem("saved-thermal-printer")) {
      const options = JSON.parse(localStorage.getItem("saved-thermal-printer"));
      const printer = new Printer({
        type: types.EPSON,
        options,
      });
      setPrinter(printer);
      setConnected(await printer.Interface.isPrinterConnected());
    }
  }, []);

  const reconnect = useCallback(async () => {
    setConnected(await printer.Interface.isPrinterConnected());
  }, [printer]);

  useEffect(() => {
    if (WebUSB.isWebUSBAvailable()) {
      navigator.usb.onconnect = () => reconnect();
      navigator.usb.ondisconnect = () => reconnect();
    }
  }, [reconnect]);

  const request = useCallback(async () => {
    const usb = await WebUSB.requestPrinter();
    const options = {
      productId: usb.productId,
      vendorId: usb.vendorId,
    };
    localStorage.setItem(
      "saved-thermal-printer",
      JSON.stringify({
        productId: usb.productId,
        vendorId: usb.vendorId,
      })
    );
    const printer = new Printer({
      type: types.EPSON,
      options,
    });
    setPrinter(printer);
    setConnected(await printer.Interface.isPrinterConnected());
  }, []);

  return { isSupported, request, restore, printer, connected, reconnect };
}

function App() {
  const { isSupported, request, restore, printer, connected, reconnect } =
    useThermalPrinter();

  useEffect(() => {
    restore();
  }, [restore]);

  return (
    <div className="App">
      <div>WebUSB: {isSupported ? "supported" : "not available"}</div>
      {isSupported && (
        <div>
          {printer ? (
            <div>
              {connected ? (
                <div>
                  <span>Connected</span>
                  <button
                    onClick={async () => {
                      const imageData = await urlToRaw("./sample.jpg");
                      await printer.printImageBuffer(imageData);
                      await printer.cut();
                      await printer.execute();
                    }}
                  >
                    Print
                  </button>
                </div>
              ) : (
                <div>
                  <span>Disconnected</span>
                  <div>
                    <button onClick={reconnect}>Refresh</button>
                    <button onClick={request}>Connect other device</button>
                  </div>
                </div>
              )}
            </div>
          ) : (
            <div>
              <button onClick={request}>Connect</button>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

export default App;
